<template>
  <div class="phyExamination">
    <phy-examination-options @search="search" ref="phyOptions"/>
    <phy-examination-table :loading="loading" @search="search" :phyTotalSize="totalSize" :data="data" :pageShow="pageShow" ref="phyTable"/>
  </div>
</template>

<script>
import PhyExaminationOptions from './controls/phyExaminationOptions'
import PhyExaminationTable from './controls/phyExaminationTable'
import queryString from "querystring"

export default {
  name: 'phyExamination',
  data() {
    return {
      data:[], //table数据
      totalSize: 0, //一共多少条数据
      pageShow: false,  //页码切换是否显示
      loading: false
    }
  },
  components: {
    PhyExaminationOptions,
    PhyExaminationTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.phyOptions.getData();
      const tableParams = this.$refs.phyTable.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.axios.get(`/physical?${param}`).then((res)=>{
        this.loading = false;
        this.data = res.data.data.list;
        this.totalSize = res.data.data.totalPage;
        this.pageShow = this.data.length > 20;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    }
  }
}
</script>
 
